class DetailsSection extends React.Component {
  render(){
    return(
      <div className="w3-row details-section">
        <div className="w3-col s12 l5">
          <div className="img-container">
            <img src={`./assets/img/${this.props.img}`} alt={this.props.name} className="w3-image" />
          </div>
        </div>
        <div className="w3-col s12 l7 w3-padding">
          <p className="returnLink">
            <a onClick={this.props.hideBlock} href="#portfolio" title="Regresar">Regresar</a>
          </p>
          <h3>{this.props.name}</h3>
          <h4><strong>{this.props.enterprise}</strong></h4>
          <p className="date-txt">Fecha: {this.props.date}</p>
          {this.props.url && <p className="date-txt">Enlace: <a href={this.props.url} title={this.props.name} target="_blank" rel="noopener noreferrer">{this.props.url}</a></p>}
          <p>Detalles: {this.props.details}</p>
        </div>
      </div>
    )
  }
}

function BlockElem(props) {
  return(
    <div>
      <img src={`./assets/img/thumb-${props.img}`} alt={props.name} className="w3-image" />
      <h3>{props.name}</h3>
    </div>
  );
}

class PortfolioSection extends React.Component{
  constructor(props) {
    super(props);
    
    this.state = {
      projectId: this.props.projects[0].id,
      projectName: this.props.projects[0].name,
      projectImg: this.props.projects[0].img,
      projectDate: this.props.projects[0].date,
      projectEnterprise: this.props.projects[0].enterprise,
      projectUrl: this.props.projects[0].url,
      projectDetails: this.props.projects[0].details,
      isVisible: false
    }
  }

  displayProject = (thisElem, idParam, nameParam, dateParam, enterpriseParam, imgParam, urlParam, detailsParam) => {
    this.setState({
      projectId: idParam,
      projectName: nameParam,
      projectImg: imgParam,
      projectDate: dateParam,
      projectEnterprise: enterpriseParam,
      projectUrl: urlParam,
      projectDetails: detailsParam,
      isVisible: true
    })
  }

  hideBlock = (e) => {
    e.preventDefault()
    this.setState({
      isVisible: false
    })
    window.scrollTo(0, 1300)
  }

  render() {
    let blocks = [],
        detailsBlock,
        showGrid = '',
        indArray = 0
    const finalArr = [],
          projectName = this.state.projectName,
          projectDate = this.state.projectDate,
          projectEnterprise = this.state.projectEnterprise,
          projectImg = this.state.projectImg,
          projectUrl = this.state.projectUrl,
          projectDetails = this.state.projectDetails,
          isVisible = this.state.isVisible

    this.props.projects.forEach((project, index) => {
      indArray = index+1
      let handleClick = (e) => {
        e.preventDefault()
        this.displayProject(this, project.id, project.name, project.date, project.enterprise, project.img, project.url, project.details)
        
        window.scrollTo(0, 1300)
      }  
      blocks.push(
        <div key={index} className="w3-col s6 m3 l3 w3-padding">
          <a href="#portfolio" onClick={handleClick} name={project.name} date={project.date} enterprise={project.enterprise} img={project.img} url={project.url} details={project.details}>
            <BlockElem id={project.id} name={project.name} date={project.date} enterprise={project.enterprise} img={project.img} url={project.url} details={project.details}></BlockElem>
          </a>
        </div>
      )
      if((indArray % 4) === 0) {
        finalArr.push(
          <div key={index} className="w3-row grid-section">{blocks}</div>
        )
        blocks = []
      }
      else if(indArray === 24) {
        finalArr.push(
          <div key={index} className="w3-row grid-section">{blocks}</div>
        )
        blocks = []
      }
      // else if((indArray+1) % 4 === 0) {
      //   finalArr.push(
      //     <div key={index} className="w3-row grid-section">{blocks}</div>
      //   )
      //   blocks = []
      // }
    });
    

    if(isVisible){
      detailsBlock = <DetailsSection
                      name={projectName}
                      img={projectImg}
                      date={projectDate}
                      enterprise={projectEnterprise}
                      url={projectUrl}
                      details={projectDetails}
                      isVisible={isVisible}
                      hideBlock={this.hideBlock}>
                    </DetailsSection>
      showGrid = 'showGrid'
    }else{
      detailsBlock = ''
      showGrid = ''
    }

    console.log(isVisible)

    return(
      <div className={`w3-container ${showGrid} portfolio-section`}>
        <h2 name="portfolio" id="portfolio">Portafolio</h2>
        {finalArr}
        {detailsBlock}
      </div>
    )
  }
}

